<template>
	<div class="ChoiceGoods">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>选择商品</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td>选择</td>
						<td  @click="SortArr('Consignment_ChoiceData','Sku','SortCondition1',$event)">商品代码</td>
						<td  @click="SortArr('Consignment_ChoiceData','QtyTotal','SortCondition2',$event)">数量</td>
						<td  @click="SortArr('Consignment_ChoiceData','QtyScan','SortCondition3',$event)">已采集</td>
						<td  @click="SortArr('Consignment_ChoiceData','Descr','SortCondition4',$event)">商品名称</td>
						<td  @click="SortArr('Consignment_ChoiceData','Susr2','SortCondition5',$event)">单位</td>
						<td  @click="SortArr('Consignment_ChoiceData','QtyCase','SortCondition6',$event)">箱数</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in Consignment_ChoiceData'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.Sku}}</td>
						<td>{{dataItem.QtyTotal}}</td>
						<td>{{dataItem.QtyScan}}</td>
						<td>{{dataItem.Descr}}</td>
						<td>{{dataItem.Susr2}}</td>
						<td>{{dataItem.QtyCase}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"ChoiceGoods",
		data(){
			return{
				Consignment_ChoiceData:[],//列表数据
				detail_item:0,//默认为第一个
				SortCondition1:true,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
				SortCondition6:false,
				SortIcon1:''
			}
		},
		mounted(){
			$("thead").find("td").eq(0).siblings().append('<i class="iconfont"></i>')
		},
		created(){

			console.log(this.$store.state.Consignment_ChoiceData)

			

			if(this.$store.state.Consignment_ChoiceData.length){
				var Consignment_ChoiceData = this.$store.state.Consignment_ChoiceData;

				this.Consignment_ChoiceData = Consignment_ChoiceData;
			}
		},
		methods:{
			// 返回上一页 页面
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ConsignmentIndex",
					query:{
						InvoiceNum:that.$route.query.InvoiceNum,//发货单号
						CommodityCode:that.$route.query.CommodityCode,//商品代码
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;
			},
			// 点击确认按钮 跳转到录入单号页面
			selectData_submit:function(){

				this.$store.state.Consignment_SubmitData = [];

				this.$store.state.Consignment_SubmitData[0] = this.Consignment_ChoiceData[this.detail_item];

				this.$router.push({
					name:"ConsignmentNumCollect",
					query:{
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.ChoiceGoods{
		&>.container{
			width:100%;
			padding-bottom: 130/50rem;
			table{
				tr{
					td:nth-child(2){
						min-width: 170/50rem;
					}
					td:nth-child(4){
						min-width: 140/50rem;
					}
					td:nth-child(5){
						min-width: 330/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}

	@media screen and (max-width: 332px) and (min-width: 251px){
		.ChoiceGoods{
			&>.container{
				table{
					tr{
						td:nth-child(2){
							min-width: 200/50rem;
						}
						td:nth-child(4){
							min-width: 200/50rem;
						}
						td:nth-child(5){
							min-width: 400/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}

	@media screen and (max-width: 250px){
		.ChoiceGoods{
			&>.container{
				table{
					tr{
						td:nth-child(2){
							min-width: 260/50rem;
						}
						td:nth-child(4){
							min-width: 210/50rem;
						}
						td:nth-child(5){
							min-width: 450/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


